/*GENERAL*/

.container {
	max-width: 150rem;
	margin: 0 auto;
	padding: 0 3.2rem;
}

/*GENERAL*/

/*STYLE*/

.deactive_return {
	display: none;
}

.header {
	display: flex;
	position: relative;
	justify-content: space-between;
	align-items: center;
	height: 9.6rem;
	padding: 0 4.8rem;
	z-index: 9999;
}

.btn-mobile--nav {
	display: none;
	border: none;
	background: none;
	cursor: pointer;
}

.icon-mobile-nav {
	height: 4.8rem;
	width: 4.8rem;
	color: #636363;
}

.icon-mobile-nav[name="close-outline"] {
	display: none;
}

.form-control,
.form-select {
	width: 100%;
	padding: 0.8rem;
	font-size: 1.6rem;
	font-family: inherit;
	color: inherit;
	border-radius: 9px;
	box-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
}

.card *:focus {
	outline: none;
	box-shadow: 0 0 0 0.8rem rgba(253, 242, 233, 0.5);
}

.form-labels,
.form-text,
.input-text-group,
.children-input,
.infants-input {
	color: white;
	font-size: 16px;
}

.form-labels {
	display: inline-block;
	margin-bottom: 2rem;
}

.form-labels__passangers {
	color: white;
	font-size: 24px;
	margin-top: 24px;
}

.form-labels p {
	margin-bottom: 14px;
}

.logo {
	text-decoration: none;
	color: #7ecd17;
	font-size: 25px;
	font-weight: bold;
	height: 2.2rem;
}

.card {
	display: grid;
	gap: 4.8rem;
	grid-template-columns: 1fr 1fr;
	grid-template-rows: 0.4fr 1fr;

	.card-body {
		display: block;
		padding: 20px 40px;
		border: 2px solid #bebebe;
		background-color: #69696985;
		border-radius: 10px;
		box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);

		.card-title {
			color: white;
			margin: 0 0 2.4rem 0;
			font-size: 1.6rem;
			font-weight: bold;
		}

		.card-title-currency {
			margin-top: 35px;
			margin-bottom: -1px;
			color: white;
		}

		.card-location {
			display: flex;
			margin-top: 2.4rem;
			gap: 50px;
			justify-content: space-between;
			align-items: center;
		}

		.card-dates {
			display: flex;
			align-items: center;
			gap: 100px;

			.dates-container {
				display: flex;
				gap: 24px;
			}
		}

		.passanger-inputs {
			display: flex;
			flex-direction: column;
			gap: 24px;
		}
	}

	.search-button {
		display: inline-block;
		margin-right: 50px;
		margin: 0 auto;
		width: 335px;
		grid-column: -3/-1;
		border-radius: 15px;
		cursor: pointer;
		margin-top: 80px;
		height: 30px;
	}

	.search-button:hover {
		background-color: orangered;
	}

	.cancel-btn {
		margin-top: 55px;
		border-radius: 10px;
		height: 35px;
		width: 30%;
		display: inline-block;
	}
}

#search-button-again {
	position: fixed;
	top: 155px;
	right: 55px;
	width: 120px;
	z-index: 1000;
	height: 50px;

	border-radius: 15px;
	border: solid 1px black;
}

.hidden {
	display: none;
}

.hidden-currency {
	display: none;
}

.main-nav-list {
	display: flex;
	align-items: center;
	list-style: none;
	gap: 4.8rem;
}

.main-nav-link:link,
.main-nav-link:visited {
	display: inline-block;
	text-decoration: none;
	color: #f5f4f4;
	font-weight: 500;

	transition: all 0.3s;
}

.nav-cta {
	margin-left: 18rem;
}

.heading-primary {
	color: white;
	font-size: 4.4rem;
	line-height: 1.05;
	margin-bottom: 3.2rem;
}

.hero-description {
	font-size: 2rem;
	line-height: 1.6;
	margin-bottom: 4.2rem;
	background-image: cover;
	z-index: 2;
}

.section-hero {
	max-width: 130rem;
	position: relative;

	margin: 0 auto;
	z-index: 1;
}

.hero {
	overflow: hidden;
}

.hero-text-box {
	text-align: center;
}

.hero-description {
	color: #f5f4f4;
}

#currencyAdd {
	min-width: 80px;
	border-radius: 15px;
	height: 50px;
	text-align: center;
	position: absolute;
	top: 155px;
	right: 350px;
}

