// LOGIN FORM

//LOGIN //REGISTER// CREATE ACCOUNT// SINGOUT

.nav-link {
	margin-top: 5px;
	width: 200px;
	padding: 13px 20px;
	outline: none;
	border: none;
	font-size: 15px;
	font-weight: 600;
	border-radius: 28px;
	cursor: pointer;
}

a#user-link.nav-link {
	text-decoration: none;
	font-weight: 500;
	font-size: 20px;
	color: #7ecd17;
}

.login {
	margin-top: 5px;
	display: inline-block;
	padding: 13px 20px;
	outline: none;
	border: none;
	font-size: 15px;
	font-weight: 600;
	border-radius: 28px;
	cursor: pointer;
	text-decoration: none;
	color: white;
	background-color: green;
}

.icon-mobile-nav {
	color: #fff;
}

.basic-button {
	background-color: royalblue;
	text-decoration: none;
}

#login {
	margin-top: 5px;
	width: 100%;
	padding: 13px 20px;
	outline: none;
	border: none;
	font-size: 15px;
	font-weight: 600;
	border-radius: 18px;
	cursor: pointer;
}

.login-header {
	font-size: 2rem;
	color: #07001f;
	margin-bottom: 1.2rem;
}

form input {
	width: 92%;
	outline: none;
	border: 1px solid #fff;
	padding: 12px 20px;
	margin-bottom: 40px;
	border-radius: 20px;
	background: #e4e4e4;
}

.login-submit {
	font-size: 1.4rem;
	font-weight: 500;
	margin-top: 4rem;
	padding: 10px 0;
	border-radius: 20px;
	outline: none;
	border: none;
	width: 90%;
	color: #fff;
	cursor: pointer;
	background: rgb(17, 107, 143);
}

.login-submit:hover {
	background: rgba(17, 107, 143, 0.7);
}

input:focus {
	border: 1px solid rgb(192, 192, 192);
}

.member {
	font-size: 1.2rem;
	margin-top: 1.4rem;
	color: #636363;
}

.member a {
	color: rgb(17, 107, 143);
	text-decoration: none;
}

.recovery {
	text-align: right;
	font-size: 1rem;
	margin: 0.3rem 1.4rem 0 0;
}

.recovery a {
	text-decoration: none;
	color: #464647;
}

