.popup-overlay {
	position: fixed;
	top: 0px;
	left: 0px;
	width: 100%;
	height: 100vh;
	z-index: -500;
	background: rgba(0, 0, 0, 0.5);
	display: none;
}

.popup {
	position: absolute;
	top: 50%;
	left: 50%;
	height: 420px;
	margin: 50px auto;
	padding: 2rem 1rem;
	border-radius: 10px;
	text-align: center;
	background: #f5f4f5;
	z-index: 2;
	opacity: 0;
	box-shadow: 5px 5px 3px rgba(0, 0, 0, 0.2);
	transform: translate(-50%, -50%) scale(1.15);
	transition: transform 300ms ease-in-out, opacity 300ms ease-in-out;
	width: 330px;
}

body.showLoginForm .popup-overlay {
	display: block;
}

body.showLoginForm .popup {
	top: 50%;
	z-index: 2;
	opacity: 1;
	transform: translate(-50%, -50%) scale(1);
}

.popup .popup-close {
	position: absolute;
	top: 5px;
	right: 6px;
	width: 40px;
	height: 40px;
	color: #0c0c0c;
	font-size: 25px;
	font-weight: 600;
	text-align: center;
	border-radius: 50%;
	cursor: pointer;
}

.table-info-open {
	display: none;
}

.table-show {
	color: #ffffff;
	font-size: 0.9rem;
}

table {
	font-size: 12px;
	text-align: center;
	background-color: transparent;
	position: absolute;
	top: 210px;
	border-collapse: collapse;
	width: 90%;
	margin: 0.8rem auto;

	thead {
		font-size: 15px;
		background-color: #6a153e;

		#currency {
			background-color: #2c2a2a;
			text-align: center;
		}
	}
}

table thead tr th {
	font-size: 14px;
	font-weight: 600;
	letter-spacing: 0.35px;
	color: #fff;
	opacity: 1;
	padding: 12px;
	vertical-align: top;
	border: 1px solid #dee2e685;
}

table tbody tr td {
	font-size: 14px;
	letter-spacing: 0.35px;
	font-weight: normal;
	color: #f1f1f1;
	background-color: #3c3f44;
	padding: 8px;
	text-align: center;
}

tr:nth-child(even) {
	color: white;
}

tr:nth-child(even) {
	background-color: rgb(17, 107, 143);
}
