* {
	padding: 0;
	margin: 0;
	box-sizing: border-box;
}

html {
	font-size: 62.5%;
	overflow-x: hidden;
	scroll-behavior: smooth;
}

body {
	font-family: "Rubik", sans-serif;
	line-height: 1;
	font-weight: 400;
	color: #555;
	height: 100%;
	overflow-x: hidden;
	background-image: url("../assets/img/hero.jpg");
	background-size: cover;
	background-repeat: no-repeat;
}

.main-nav-link:hover,
.main-nav-link:active {
	color: #333;
	background-color: aliceblue;
	padding: 10px 15px;
	border-radius: 10px;
}

.grid {
	display: grid;
	-moz-column-gap: 6.4rem;
	     column-gap: 6.4rem;
	row-gap: 9.6rem;
}


