/*SPACING SYSTEM (px) 
2 / 4 / 8 / 12 / 16 / 24 / 32 / 48 / 64 / 80 / 96 / 128

FONT SIZE SYSTEM (px)
10 / 12 / 14 / 16 / 18 / 20 / 24 / 30 / 36 / 44 / 52 / 62 / 74 / 86 / 98

*/

/* np 1350px/16= 84em */
/**************/
/* BELOW 1344px (smaller desktops)*/

@media (min-width: 95em) {
	header {
		height: 10rem;
	}
	.hero {
		margin: 6.4rem 0 6.4rem 0;
	}

	.section-hero {
		max-width: 140rem;

		margin-top: 4rem;
	}

	.heading-primary {
		font-size: 3.6rem;
	}

	.main-nav-list {
		font-size: 2.4rem;
		align-items: center;
		gap: 7.8rem;
	}

	a.logo {
		font-size: 3.4rem;
		width: 35rem;
		letter-spacing: 0.8px;
	}

	a#user-link,
	a#sign-out-link,
	a.main-nav-link {
		font-size: 1.8rem;
	}
}

@media (min-width: 75em) and (max-width: 95em) {
	header {
		height: 10rem;
	}
	.hero {
		margin: 6.4rem 0 6.4rem 0;
	}

	.section-hero {
		max-width: 125rem;

		margin-top: 4rem;
	}

	.heading-primary {
		font-size: 3.6rem;
	}

	.main-nav-list {
		font-size: 2.4rem;
		align-items: center;
		gap: 7.8rem;
	}

	a.logo {
		font-size: 3.4rem;
		width: 35rem;
		letter-spacing: 0.8px;
	}

	a#user-link,
	a#sign-out-link,
	a.main-nav-link {
		font-size: 1.8rem;
	}

	a#user-link {
		min-width: 23.5rem;
	}
}

/**************/
/* BELOW 1200px (landscape tablets)*/
/**************/

@media (max-width: 75em) {
	html {
		/* 9px/16px=0.5625 */
		font-size: 56.25%;
	}

	.section-hero {
		max-width: 120rem;
	}

	.hero {
		margin: 4.8rem 0 4.8rem 0;
	}

	.card {
		-moz-column-gap: 4.8rem;
		     column-gap: 4.8rem;
		row-gap: 6.4rem;
	}

	.header {
		padding: 0 4.8rem;
	}

	.main-nav-list {
		gap: 4.8rem;
	}

	.card .card-body .card-title {
		font-size: 1.6rem;
	}
}

@media (max-width: 75em) {
	html {
		/* 9px/16px=0.5625 */
		font-size: 56.25%;
	}

	.header {
		padding: 0 4.8rem;
	}

	.heading-primary {
		font-size: 3.6rem;
	}

	.main-nav-list {
		gap: 4.4rem;
	}

	a#user-link,
	a#sign-out-link,
	a.main-nav-link {
		font-size: 2rem;
	}

	.card .card-body .card-title {
		font-size: 1.2rem;
	}
}

@media (max-width: 60em) {
	/* MOBILE NAVIGATION */

	.btn-mobile--nav {
		display: block;
		z-index: 1000;
	}

	.main-nav {
		background-color: rgba(255, 255, 255, 0.97);
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100vh;
		transform: translateX(100%);

		display: flex;
		align-items: center;
		justify-content: center;
		transition: all 0.5s ease-in;
		/* Allows NO transitions at all */
		/* display: none; */

		/* 1) Hide it visually */
		opacity: 0;
		/* 2) Make it unaccessible to mouse and keyboard */
		pointer-events: none;
		/* 3)Hide it form screen readers */
		visibility: hidden;
	}

	.nav-open .main-nav {
		opacity: 1;
		pointer-events: auto;
		visibility: visible;
		transform: translateX(0);
	}

	.nav-open .icon-mobile-nav[name="close-outline"] {
		display: block;
	}

	.nav-open .icon-mobile-nav[name="menu-outline"] {
		display: none;
	}

	.main-nav-list {
		flex-direction: column;
		gap: 4.8rem;
	}

	.main-nav-link:link,
	.main-nav-link:visited {
		color: #333;
		font-size: 3rem;
	}

	#flight-type-select {
		min-width: 12.5rem;
	}

	#origin-input,
	#destination-input {
		min-width: 11.5rem;
	}
}

@media (max-width: 56em) {
	.card {
		grid-template-columns: 1fr;
	}

	div.card-body {
		max-width: 100rem;
	}
}

/**************/
/* BELOW 544px (Tablets)*/
/**************/

@media (max-width: 34em) {
	.btn,
	.btn:link,
	.btn:visited {
		padding: 2.4rem 1.6rem;
	}

	.card-title {
		display: none;
	}

	div.card-body {
		max-width: 35rem;
	}

	.hero {
		padding: 0 3.2rem;
		gap: 6.4rem;
	}

	.hero-img {
		width: 80%;
	}

	a.logo {
		font-size: 1.8rem;
	}

	.section-hero {
		padding: 2.4, 0 4.8rem 0;
	}

	table {
		overflow-y: scroll;
		width: 100%;
		height: 500px;
	}
	

	table {
		margin-top: 100px;
	}
	table thead {
		display: none;
	}

	table,
	table tbody,
	table tr,
	table td {
		display: block;
	}

	table tr {
		margin-bottom: 15px;
		border-radius: 10px;
	}

	table tbody tr td {
		/* text-align: right; */
		padding-left: 35%;
		position: relative;
	}

	table td:before {
		content: attr(data-label);
		position: absolute;
		left: 0;
		width: 15%;
		padding-left: 15px;
		font-weight: 600;
		font-size: 14px;
		text-transform: capitalize;
	}
}
